.zen-blocks-admin td {
  vertical-align: middle;
}
.zen-blocks-admin-header {
  display: inline-flex;
  align-items: center;
}
.zen-blocks-admin .components-notice {
  margin: 20px 0;
}
.zen-blocks-admin-block-title {
  font-size: 16px;
  font-weight: bold;
}
.zen-blocks-admin-block-badge {
  background: #c0c1c2;
  color: white;
  padding: 2px 8px;
  border-radius: 4px;
