.zen-blocks-control {
  margin-bottom: 20px;
}
.zen-blocks-control-placeholder {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f0f0f0;
  padding: 20px;
  border-radius: 4px;
  border: 1px solid #e0e0e0;
  transition: all 0.2s ease;
}
.zen-blocks-control-placeholder button {
  pointer-events: none;
}
.zen-blocks-control-placeholder:hover {
  border-color: var(--wp-admin-theme-color);
}
.zen-blocks-control-placeholder.is-active {
  border-color: var(--wp-admin-theme-color);
  box-shadow: 0 0 0 1px var(--wp-admin-theme-color);
}
.zen-blocks-control-header {
  display: flex;
  align-items: center;
  gap: 15px;
  justify-content: space-between;
  width: 100%;
}
.zen-blocks-control-actions {
  margin: 15px auto;
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}
.zen-blocks-control-actions-add svg {
  width: 20px;
  height: 20px;
}
.zen-blocks-control-actions-add:hover {
  background-color: var(--zen-primary);
  color: white !important;
}
.zen-blocks-control-panel {
  margin: 10px -15px 20px;
  border: none;
}
.zen-blocks-control-editor {
  padding: 15px;
  border-top: 1px solid var(--zen-border);
}
.zen-blocks-control-editor .components-base-control {
  margin-bottom: 20px;
}
.zen-blocks-control-editor .components-base-control:last-child {
  margin-bottom: 0;
}
.zen-blocks-control-editor .components-base-control .components-base-control__label {
  display: block;
  font-weight: 600;
  margin-bottom: 5px;
  color: var(--zen-text);
}
.zen-blocks-control-editor .components-base-control .components-text-control__input,
.zen-blocks-control-editor .components-base-control .components-select-control__input {
  border-color: var(--zen-border);
  border-radius: 3px;
  box-shadow: 0 0 0 transparent;
  font-size: 14px;
  line-height: 2;
  min-height: 30px;
  padding: 0 8px;
}
.zen-blocks-control-editor .components-base-control .components-text-control__input:focus,
.zen-blocks-control-editor .components-base-control .components-select-control__input:focus {
  border-color: var(--zen-primary);
  box-shadow: 0 0 0 1px var(--zen-primary);
}
.zen-blocks-control-editor .components-base-control.has-error .components-text-control__input {
  border-color: #cc1818;
}
.zen-blocks-control-editor .components-base-control.has-error .components-base-control__help {
  color: #cc1818;
}
.zen-blocks-control-option {
  display: flex;
  gap: 15px;
  align-items: flex-end;
  margin-bottom: 10px;
}
.zen-blocks-control-option-actions {
  margin-bottom: 15px;
}
.zen-blocks-control-option .components-base-control {
  margin-bottom: 0;
  flex: 1;
}
.zen-blocks-control-options {
  border-top: 1px solid var(--zen-border);
}
.zen-blocks-control-options-actions {
  margin-top: 20px;
}
.zen-blocks-control-code-snippet {
  margin-top: 16px;
  padding-top: 16px;
  border-top: 1px solid #e0e0e0;
}
.zen-blocks-control-code-snippet__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}
.zen-blocks-control-code-snippet__header span {
  font-weight: 500;
  color: #1e1e1e;
}
.zen-blocks-control-code-snippet__php {
  margin: 0;
  padding: 12px;
  background: #f0f0f0;
  border-radius: 4px;
  font-family: monospace;
  font-size: 13px;
  line-height: 1.4;
  color: #1e1e1e;
}
.zen-blocks-control-code-snippet__example {
  margin-top: 12px;
  color: #666;
}
.zen-blocks-control-code-snippet__example span {
  display: block;
  margin-bottom: 4px;
  font-size: 12px;
}
.zen-blocks-control-code-snippet__example pre {
  margin: 0;
  padding: 8px 12px;
  background: #f7f7f7;
  border-radius: 4px;
  font-family: monospace;
  font-size: 13px;
  line-height: 1.4;
  color: #666;
}

